<template>
  <div>
    <Breadcrumb class="mb-8" page-title="Changelog" :backTo="backTo" :items="breadcrumb" />
    <Card hide-footer class="mt-8">
      <template #header>
        <h6 class="font-semibold text-lg">Latest Changelog</h6>
      </template>
      <div :style="{maxHeight: '65vh'}" class="overflow-y-auto">
         <Changelog collapse child-key="data" date-key="month" :items="state">
          <template #description="scoped">
            <div class="mr-6">
              <span class="block text-right">{{scoped.item.created_at}}</span> 
              <span class="block text-right text-sm">by {{scoped.item.admin}}</span>
            </div>
          </template>
          <template #default="scoped">
            <ul class="list">
              <li v-for="(log, logIndex) of scoped.item.logs" v-html="sanitize(log.desc)" :key="logIndex" class="text-grey-04" />
            </ul>
          </template>
        </Changelog>
      </div>
    </Card>
  </div>
</template>

<script>
import { defineComponent, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useChangelog } from 'composable/buyback'
import Breadcrumb from 'components/Breadcrumb.vue'
import Card from 'bbUi/components/card/card.vue'
import Changelog from 'components/Changelog.vue'

import { sanitize } from 'utils/dom'

export default defineComponent({
  components: {
    Breadcrumb,
    Card,
    Changelog
  },
  props: {
  },
  async setup() {
    const { state, loading, action } = useChangelog()
    const route = useRoute()
    const backTo = computed(() => {
      const path = route.path.split('/').filter(item => item !== 'changelog').join('/')
      return ({
        path: path
      })
    })
    const breadcrumb = computed(() => [{
      text: 'Mail-In',
      to: {
        name: 'buybackOnline'
      }
    }, {
      text: 'Offer Detail',
      to: backTo.value
    },
    {
      text: 'Changelog'
    }])
    await action({
      id: route.params.buybackId
    })
    return {
      state,
      loading,
      backTo,
      breadcrumb,
      route,
      sanitize
    }
  }
})
</script>